<template>
  <div id="app">
    <div v-if="isLoading">Loading ......</div>
    <div v-else>
      <div v-for="item in getItemsList" :key="item.id">
        {{ item.name }} - {{ item.lastName }} - {{ item.room }}
        <button @click="deleteItem(item.id)">Delete</button>
      </div>
      <br />

      <div>
        <div>
          title
          <input v-model="list.name" type="string" />
        </div>
        <div>
          price
          <input v-model="list.lastName" type="string" />
        </div>
        <div>
          image src
          <input v-model="list.room" type="number" />
        </div>
        <button @click="addItem(list)">Add</button>
      </div>

      <hr />
    </div>

    <router-view />
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "ListView",
  data() {
  return {
    list: {
      name: '',
      lastName: '',
      room: 0,
    },
  };
},
  computed: {
    ...mapGetters("list", ["getItemsList", "isLoading"]),
  },
  created() {
    this.loadList();
  },
  methods: {
    ...mapActions("list", [
      "loadList",
      "addItem",
      "deleteItem",
      "updateItem",
      "loadFilteredData",
    ]),
    onAddPrice(item) {
      this.updateItem({
        itemId: item.id,
        data: {
          price: item.price + 100,
        },
      });
    },
  },
};
</script>
